<template>
  <div class="login-page">
    <div class="login-logo">
      <img :src="loginLogoImg" alt="" @click="changeBaseUrl">
    </div>

    <van-tabs 
    v-model="activeTab"
    swipeable
    :border="false"
    line-height="3"
    line-width="25"
    color="#FB5768">
        <van-tab title="验证码登录">
          <div class="phone-number">
            <input 
              type="tel"
              class="input-phone"
              placeholder="输入手机号"
              maxlength="11"
              v-model="phoneNum"
              onkeyup="value=value.replace(/[^\d]/g,'')"
              @keydown.13="focusChange"
            />
          </div>
          <div class="phone-authcode">
            <input 
              type="tel"
              class="input-authcode"
              placeholder="输入验证码"
              maxlength="6"
              v-model="phoneAuthCode"
              onkeyup="value=value.replace(/[^\d]/g,'')"
              ref="authcodeFocus"
              @keydown.13="getPubKey"
            />
            <div class="get-verification" :style="{color: verifyStrColor}" @click="getPhoneValit">{{verifyCodeStr}}</div>
          </div>
          <div class="protocol">
            <div class="custom-checkbox" @click="isAgreeprotocol = !isAgreeprotocol">
              <img :src="protocolCheckImg">
            </div>
            <span class="agree-text">我已阅读并同意</span>
            <span class="protocol-text">《</span>
            <span class="protocol-text" style="text-decoration: underline" @click="clickedProtocol(1)">信我家服务协议</span>
            <span class="protocol-text">》、《</span>
            <span class="protocol-text" style="text-decoration: underline" @click="clickedProtocol(2)">隐私保护协议</span>
            <span class="protocol-text">》</span>
          </div>
        </van-tab>

        <van-tab title="密码登录">
            <div class="phone-number">
            <input 
              type="tel"
              class="input-phone"
              placeholder="输入手机号"
              maxlength="11"
              v-model="phoneNum"
              onkeyup="value=value.replace(/[^\d]/g,'')"
              @keydown.13="focusChange"
            />
          </div>
          <div class="phone-password">
            <input 
              type="password"
              class="input-password"
              placeholder="输入密码"
              maxlength="16"
              v-model="password"
              ref="passwordFocus"
              @keydown.13="getPubKey"
            />
          </div>
          <div class="forget-password" @click="forgetPwd()">
            忘记密码?点此重置
          </div>
        </van-tab>
    </van-tabs>
    <!-- <div class="login-btn" :style="{'background': 'url('+ loginBtnBackground + ') center no-repeat'}" @click="clickedLoginBtn"> -->
<!--    <div class="login-btn" :style="{'background-color': loginBtnBackground, 'box-shadow': loginBtnShadow}" @click="clickedLoginBtn">-->
    <div class="login-btn" :style="{'background-color': loginBtnBackground, 'box-shadow': loginBtnShadow}" @click="getPubKey">
      登录
    </div>

    <!-- 弹出框 -->
    <van-dialog 
    v-model="isShowDialog"
    confirmButtonColor="#fb5768"
    :message="dialogMessage"
    show-cancel-button
    :beforeClose="beforeClose"/>
    <reset-server ref="resetServer"/>
  </div>
</template>

<script>
import config from '../../common/config'
import {
  getPhoneValitService,
  requestLoginService,
  getRsaPublicKey
} from "../../services/loginService";

import Vue from 'vue';
import {Tab, Tabs, Dialog} from 'vant';
import {storage} from '../../utils/storage'
Vue.use(Tab).use(Tabs).use(Dialog);
import {goWXShareDetail}  from '../../utils/skipLinkManage'


// 登录logo
import loginLogoImg from '@/assets/images/common/login_logo.png'
//协议勾选图片
import checkBoxImg from '@/assets/images/common/chebox_choice.png'
//协议未勾选图片
import uncheckBoxImg from '@/assets/images/common/check_default.png'
//切换网络环境组件
import ResetServer from '../Mine/ResetServer'
export default {
  name: "login",
  components: {
    ResetServer
  },
  data() {
    return {
      verifyCodeStr: '获取验证码',
      verifyStrColor: '#FB5768',
      phoneNum: '',
      phoneAuthCode: '',
      password: '',
      // 是否同意协议
      isAgreeprotocol: false,
      //登录按钮使能
      loginBtnEnable: false,
      //当前显示的tab
      // activeTab: 0,
      //是否显示提示框
      isShowDialog: false,
      //提示框内容
      dialogMessage: '',
      // 0： 表示未注册 1：未设密码
      dialogType: 0,
      // 验证码倒计时计时器
      vCodeClock: {},

      //本地图片
      loginLogoImg: loginLogoImg,
      checkBoxImg: checkBoxImg,
      uncheckBoxImg: uncheckBoxImg,
      //点击logo切换环境 点击次数
      clickTimes:0,
      publicKey:'',
      rsaPassWord:'',
    };
  },
  mounted(){
    let protocolFlag = storage.getStorage('protocalCheckFlag')
    if (!protocolFlag.$s_code_$exp) {
      //有值
      this.isAgreeprotocol = protocolFlag;
    }
    // this.getPubKey();
  },
  activated() {
    // 检查用户是否登录
    this.checkUserLogin();
  },
  computed:{
    activeTab: {
      get() {
        return this.$store.state.login.activeTab;
      },
      set(newVal) {
        this.$store.dispatch('changeActiveTabFun', newVal);
      }
    },
    //协议选择box图片
    protocolCheckImg: function() {
      return this.isAgreeprotocol ? checkBoxImg : uncheckBoxImg;
    },
    //登录按钮背景色
    loginBtnBackground: function() {
      return this.loginBtnEnable ? '#FB5768' : '#D0CDCE';
    },
    //登录按钮阴影
    loginBtnShadow: function() {
      return this.loginBtnEnable ? '0 0 15px 0 rgba(197,58,72,0.39)' : ' 0 0 15px 0 #D0CDCE';
    }
  },
  watch: {
    // 监听手机号输入
    phoneNum: function(phoneNum) {
      this.changngeLoginBtnStatus();
    },
    //监听验证码输入
    phoneAuthCode: function(authCode) {
      this.changngeLoginBtnStatus();
    },
    //监听密码输入
    password: function(pwd) {
      this.changngeLoginBtnStatus();
    },
    activeTab: function(index) {
      // console.log('watch aactiveTab ' + index);
      this.changngeLoginBtnStatus();
    }
  },
  methods: {
    focusChange: function() {
      let isAuthcode = this.activeTab == 0 ? true : false;
      if (isAuthcode) {
        this.$refs.authcodeFocus.focus();
      }
      else {
        this.$refs.passwordFocus.focus();
      }
    },
    //修改登录按钮状态
    changngeLoginBtnStatus() {
      console.log('activeTab:' + this.activeTab);
      if (this.activeTab === 0) {
        this.loginBtnEnable = this.phoneNum.length >= 11 && this.phoneAuthCode.length >= 6;
      }
      else {
        this.loginBtnEnable = this.phoneNum.length >= 11 && this.password.length >= 6;
      }
    },
    // 获取验证码
    getPhoneValit: function() {
      if (this.verifyCodeStr != '获取验证码') {
        return;
      }
      let phoneNumreg = /^1[0-9]{10}$/;
      if (this.phoneNum === undefined || this.phoneNum.length == 0) {
        this.showMsg("请输入手机号");
        return;
      }else if (!phoneNumreg.test(this.phoneNum)) {
        this.showMsg("请输入有效的手机号码");
        return;
      }

      let reqData = {
        PHONE: this.phoneNum
      };

      // 验证码倒计时
      this.startVerifyCode();

      // 请求验证码
      getPhoneValitService(reqData)
      .then(noteData => {
        if (noteData.success == false) {
          //失败
          this.showMsg(noteData.msg);
          this.removeVerifyCode();
        }
        else {
          //成功
          this.showMsg('验证码已发送');
        }
      })
      .catch(error => {
        this.showMsg(error);
        this.removeVerifyCode();
      });   
    },
    //启动验证码倒计时
    startVerifyCode: function() {
      //修改颜色
      this.verifyStrColor = '#bdbdbd';
      let totalTime = 60;
      this.verifyCodeStr = totalTime + "秒";
      this.vCodeClock = window.setInterval(() => {
        totalTime--;
        if (totalTime > 0) {
          this.verifyCodeStr = totalTime + "秒";
        }
        else {
          this.removeVerifyCode();
        }
      }, 1000);
    },
    //移除验证码倒计时
    removeVerifyCode: function() {
      window.clearInterval(this.vCodeClock);
      //修改颜色
      this.verifyStrColor = '#FB5768';
      this.verifyCodeStr = "获取验证码";
    },
    //点击协议
    clickedProtocol: function(index) {
      //1：信我家协议 2：隐私保护协议
      let type = index == 1 ? 1 : 3;
      this.$router.push({
        name: "readTextPage",
        params: {pageType: type}
      });
    },
    // 登录提示弹框回调
    beforeClose: function(action, done) {
      if (action === "confirm") {
        if (this.dialogType == 0) {
          //跳转验证码登录
          this.activeTab = 0;
        }
        else if (this.dialogType == 1) {
          //跳转设置密码
          console.log('跳转设置密码');
          this.$router.push({
            name: "resetPwd",
            params: {phoneNum: this.phoneNum}
          });
        }
      } 
      done();
    },
    //点击忘记密码
    forgetPwd: function() {
      this.$router.push({
        name: "resetPwd",
        params: {phoneNum: this.phoneNum}
      });
    },
    //清空页面输入框
    clearPageInput() {
      this.phoneNum = '';
      this.phoneAuthCode = '';
      this.password = '';
    },
    // 判断是否可登录
    judgeLogin: function() {
      if (!this.loginBtnEnable) {
        return false;
      }
      let isAuthcode = this.activeTab == 0 ? true : false; 
      if(isAuthcode && !this.isAgreeprotocol){
        this.showMsg('请勾选海信服务协议', 'center');
        return false;
      }
    
      var myreg = /^1[0-9]{10}$/;
      if (!myreg.test(this.phoneNum)){
        this.showMsg('请输入有效的手机号码', 'center');
        return false;
      }
      return true;
    },
    //点击登录按钮
    clickedLoginBtn: function() {
      if (!this.judgeLogin()) {
        return;
      }

      let isAuthcode = this.activeTab == 0 ? true : false; 
      let reqData = {};
      if (isAuthcode) {
        //验证码
          //登录密码、验证码加入RSA公钥加密 2019/12/13
          let rsaInfo = this.phoneNum+','+this.phoneAuthCode;
          this.rsaPassWord = Vue.prototype.$getRSASign(rsaInfo,this.publicKey);
          reqData = {
            loginInfo :this.rsaPassWord
          }
      }else {
        //密码
          let basePassWord =  btoa(this.password)
          let rsaInfo = this.phoneNum+','+basePassWord;
          console.log("qiao "+rsaInfo);
          this.rsaPassWord = Vue.prototype.$getRSASign(rsaInfo,this.publicKey);
          reqData = {
            loginInfo :this.rsaPassWord
          }
      }
      requestLoginService(reqData, this.activeTab)
      .then(noteData => {
        if (noteData.resultCode == '0') {
          //成功
          if (noteData.data && noteData.data != undefined) {
            this.loginSuccessDispose(noteData.data);
            storage.setStorage("accessToken",noteData.data.accessToken);
            storage.setStorage("refreshToken",noteData.data.refreshToken,30);
          } else {
            //登录失败
            showMsg("登录失败", 'center');
          }
        }
        else {
          //失败
          this.loginFailDispose(noteData);
        }
      })
      .catch(error => {
        this.showMsg(error);
      });
    },
    //登录失败
    loginFailDispose: function(respData) {
      if (respData.msg == '您还尚未设置密码，是否前往设置？') {
        this.dialogType = 1;
        this.dialogMessage = respData.msg;
        this.isShowDialog = true;
      } 
      else if (respData.msg == '用户账号不存在！') {
        this.dialogType = 0;
        this.dialogMessage = '该手机号尚未注册，请通过验证码登录';
        this.isShowDialog = true;
      }
      else {
        this.showMsg(respData.msg);
      }
    },
    //登录成功数据处理
    loginSuccessDispose: function(respData) {

      //登录成功标识，用来解决重新登录首页不刷新问题
      Vue.prototype.$refreshHomePage = true;
      //重新登录后，需要重新上报手机型号等基础信息
      Vue.prototype.$needUpOwner = true;
      //使用验证码成功登录之后，以后服务协议默认勾选
      if (storage.getStorage('protocalCheckFlag').$s_code_$exp) {
        storage.setStorage("protocalCheckFlag", true, 999);
      }
      let userInfo = {};
      userInfo.isGygFlag = 0;//1是逛一逛标记 0不是  逛一逛不要查房间
      userInfo.OWNER_CODE = respData.ownerCode;
      userInfo.OWNER_NAME = respData.ownerName;
      userInfo.OWNER_NICK_NAME = respData.ownerNickName;
      userInfo.PHONE = respData.phone;
      userInfo.PIC_BG_URL = respData.picBgUrl;
      userInfo.PIC_URL = respData.picUrl;
      //小区信息
      if (respData.areaInfo != undefined && respData.areaInfo.length > 0) {
        userInfo.isNoArea = 0;
        let areaInfo = respData.areaInfo[0];

        // yz测试
        // if (respData.areaInfo.length > 1) {
        //   areaInfo = respData.areaInfo[1];
        // }

        userInfo.AREA_CODE = areaInfo.areaCode;
        userInfo.AREA_NAME = areaInfo.areaName;
        userInfo.BUILD_CODE = areaInfo.buildCode;
        userInfo.BUILD_NAME = areaInfo.buildName;
        userInfo.CITY_CODE = areaInfo.cityCode;
        userInfo.CITY_NAME = areaInfo.cityName;
        userInfo.DEPT_CODE = areaInfo.deptCode;
        userInfo.FLOOR_NO = areaInfo.floorNo;
        userInfo.ROOM_CODE = areaInfo.roomCode;
        userInfo.ROOM_NAME = areaInfo.roomName;
        userInfo.UNIT_NO = areaInfo.unitNo;
        userInfo.AREA_NO = areaInfo.areaNo;//小区号
        userInfo.ROOM_NO = areaInfo.roomNo;//住家号

        //更新登录信息
        Vue.prototype.$setUserInfo(userInfo);
        
        /**** 设置极光推送别名 yz未完成 *****/
        //设置标签
        let tags = [];
        for (let i = 0; i < respData.areaInfo.length; i++) {
          tags.push(respData.areaInfo[i].areaCode);
        }
        this.setTags(tags);
        this.setAlias();

        /***** flag  yz未完成 表示哪一个页面 跳到登陆页 1：o2o首页 2更多 3：我的 4：房产租售页 5店铺商品列表  6商品详情 *****/
        console.log('登录成功了啊');
        //检查是否需要微信分享跳转
        this.checkWXShare();
        //替换成首页
        this.$router.replace({ path: "/productPage" });
        switch (this.$enterPage){
              case 'YouPin':
                this.$router.replace("/productPage");
                break;
              case 'order':
                this.$router.replace("/myOrderList");
                break;
              case 'coupon':
                this.$router.replace("/couponList");
                break;
              case 'address':
                this.$router.replace("/shippingAddressList");
                break;
              case 'cart':
                this.$router.replace('./shoppingCart');
                break;
            }
        this.clearPageInput();
      } else {
        userInfo.isNoArea = 1;
        //更新登录信息
        Vue.prototype.$setUserInfo(userInfo);
        //检查是否需要微信分享跳转
        this.checkWXShare();
        // 跳转到房间绑定页面
        this.$router.push('/selectArea');
        this.clearPageInput();

        // var obj = {};
        // obj["flag"] = 2;//去绑房间1.逛一逛2.登录无绑定房间3.登录有绑定房间
        // $state.go('houseBindings', { object: JSON.stringify(obj) });
      }
    },

    checkWXShare:function(){
      //获取微信分享跳转
      let data = storage.getStorage("WXShareData");
      console.log("checkWXShare = ");
      if (!data.$s_code_$exp) {
        //删除微信分享跳转
        storage.deleteStorage("WXShareData");
        var that = this;
        setTimeout(function () {
          console.log("checkWXSharesetTimeout = ");
          goWXShareDetail(that,data);
        }, 200);
      }
    },

    //设置极光推送 别名
    setAlias: function (tags) {
      this.$plugin.usePlugin("setAlias")(
        Number(this.random()), //sequence
        this.$userInfo.OWNER_CODE, //alias
        () => { //success callback
          console.log("极光推送: 设置别名成功.");
        }, () => { //errorCallback
          console.log("极光推送：设置别名失败.");
        });
    },
    //设置极光推送 标签
    setTags: function (tags) {
      this.$plugin.usePlugin("setTags")(
        Number(this.random()), //sequence
        tags, //tags
        () => { //success callback
          console.log("极光推送：设置标签成功.");
        }, () => { //errorCallback
          console.log("极光推送：设置标签失败.");
        });
    },
    //生成随机数
    random: function () {
      var myDate = new Date();//获取系统当前时间
      var year = myDate.getFullYear().toString();
      var month = myDate.getMonth().toString();
      var date = myDate.getDate().toString();
      var hours = myDate.getHours().toString();
      var minutes = myDate.getMinutes().toString();
      var seconds = myDate.getSeconds().toString();
      var randomNumber = year + month + date + hours + minutes + seconds + Math.floor(Math.random() * 1000).toString();
      return randomNumber;
    },
    //检查用户是否登录
    checkUserLogin: function() {
      //暂时不进入首页
      return;
      let obj = storage.getStorage("userInfo");
      if (null !== obj && "数据不存在" !== obj.message) {
        //取数据到内存
        this.$router.replace({ path: "/homePage" });
      }
    },
    showMsg: function(msg){
      this.$toast(msg);
    },
    changeBaseUrl(){
      // console.log(this.clickTimes)
      // if (!(config.version.includes('RC') || config.version.includes('IT') || config.version.includes('DEV'))){
      //   console.log('appVersion 不包含 RC 或 DEV 或 IT')
      //   return
      // }
      this.clickTimes++;
      if (this.clickTimes <= 11) return;
      this.clickTimes = 0;
      Dialog.confirm({
        title: "是否需要修改网络环境"
      })
        .then(() => {
          this.$refs.resetServer.showServers = true;
          this.removeVerifyCode()
        })
        .catch(() => {
          // on cancel
        });
    },
    getPubKey(){
      if (!this.judgeLogin()) {
        return;
      }

      getRsaPublicKey().then(res => {
        this.publicKey = res.data.publicKey;
        this.clickedLoginBtn();
      }).catch(error => {
        this.isLoading =false;
        this.showMsg("网络异常，请稍后重试！");
      });
    },

  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
$inputDivHeight: 54px;
.login-page {
  height: 100%;
  width: 100%;
  background-color: #fff;
  overflow: auto;
}

.login-page .login-logo {
  padding-top: 20%;
  text-align: center;

  img {
    width: 70px;
    height: 70px;
  }
}
input {
  border:0;
  outline:none;
  background-color: transparent;
  caret-color:#fb5768;
}
input::-webkit-input-placeholder {
  /* placeholder颜色  */
  color: #9A9A9A;
  /* placeholder字体大小  */
  font-size: 14px;
  font-family: PingFangSC-Medium;
  /* placeholder位置  */
  text-align: left;
}

.login-page .van-tabs {
    padding-top: 5%;
}

//设置bottomLine样式
.van-hairline--top-bottom {
  height: 40px !important;
  line-height: 40px !important;
  border: 0 !important;
}
//设置tab居中显示
/deep/.van-tabs__nav {
  padding-right: 76px;
  padding-left: 76px;
}
//tab标题字体设置
/deep/.van-ellipsis {
  font-size: 18px;
}

/deep/.van-tab--active .van-ellipsis {
  font-family:PingFangSC-Medium;
}

/deep/.van-tab  {
  font-family:PingFangSC-Regular;
}

.phone-number,
.phone-authcode,
.phone-password {
  box-sizing: border-box;
  background: #F8F8F8;
  border-radius: 27px;
  margin: 10px 40px;
  height: $inputDivHeight;
  display: flex;
  align-items: center;
}
.phone-number {
  margin-top: 20px;
}
.input-phone,
.input-authcode,
.input-password {
  padding: 0px 32px;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  height: 32px;
  line-height: 32px;
  color: #333333;
}
.input-authcode {
  padding-right: 0px;
  width: 156px;
}
.get-verification {  
  width: 70px;
  height: $inputDivHeight;
  line-height: $inputDivHeight;
  font-family: PingFangSC-Medium;
  font-size: 14px;
  color: #FB5768;
  text-align: right;
}

.protocol {
  display: flex;
  width: 100%;
  height: 26px;
  line-height: 26px;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  padding:20px 0;
  justify-content: center;

  .custom-checkbox {
    img {
      width: 24px;
      height: 24px;
      padding-right: 4px;
    }
  }
  .agree-text {
    color: #b2b2b2;
  }
  .protocol-text {
    color: #FB5768;
  }
}

.forget-password {
    width: 130px;
    color: #fb5768;
    text-align: center;
    margin: 0 auto;
    margin-top: 5%;
    margin-bottom: 0;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    text-decoration: underline;
}

.login-page .login-btn {
  font-family: PingFangSC-Medium;
  font-size: 16px;
  width:46%;
  height: 44px;
  line-height: 44px;
  text-align: center;
  color: #f2f2f2;
  margin: 5% 27% 0;
  background-size: 100% !important;
  overflow: hidden;
  border-radius: 22px;
}

.van-dialog {
  width: 250px;
